import * as THREE from "three";
import { BoxGeometry } from "@/modules/gafu/services/geometries/BoxGeometry";
import { BasicMaterial } from "@/modules/gafu/services/materials/BasicMaterial";

const idPrefix = "cube";

export class Cube {
  geometryObj: BoxGeometry;
  materialObj: BasicMaterial;
  mesh: THREE.Mesh;
  id: string;
  constructor(color: number) {
    this.geometryObj = new BoxGeometry(1, 1, 1);
    this.materialObj = new BasicMaterial(color);
    this.mesh = new THREE.Mesh(
      this.geometryObj.geometry,
      this.materialObj.material
    );
    this.id = `${idPrefix}-${this.mesh.id}`;
  }
}
