<template>
  <div class="gafu__wrapper">
    <div class="gafu__container">
      <canvas></canvas>
    </div>
  </div>
</template>
<script>
import { GafuService } from "../services/Gafu.service";

const cubes = [];
const positionRange = 30;
for (let i = 0; i < 900; i++) {
  const cube = {
    position: {
      x:
        Math.random() *
        positionRange *
        (Math.floor(Math.random() * 2) == 1 ? 1 : -1),
      y:
        Math.random() *
        positionRange *
        (Math.floor(Math.random() * 2) == 1 ? 1 : -1),
      z:
        Math.random() *
        positionRange *
        (Math.floor(Math.random() * 2) == 1 ? 1 : -1)
    },
    color: "#000000".replace(/0/g, function() {
      return (~~(Math.random() * 16)).toString(16);
    })
  };
  cubes.push(cube);
}

export default {
  name: "gafu",
  data() {
    return {
      gafu: {},
      gafuCubes: []
    };
  },
  mounted() {
    this.gafu = new GafuService(this.$el.querySelector("canvas"));
    this.gafuCubes = cubes.map(cube => {
      return this.gafu.addObject(cube.position, cube.color);
    });
    this.gafu.setCamera({
      position: {
        z: 5
      }
    });
    this.gafu.addLight();
    const lastState = {
      direction: {
        x: 1,
        y: 1,
        z: 1
      },
      time: null
    };
    this.gafu.setNextFrameUpdater(time => {
      const newTime = time * 0.0001;
      if (!lastState.time) {
        lastState.time = time;
      }
      this.gafuCubes.forEach((gafuCube, index) => {
        if (!gafuCube.mesh) {
          return;
        }
        const speed = 0.1 * index + 1;
        const rotation = newTime * speed;
        const moveSpeed = newTime * 0.01;
        gafuCube.mesh.rotation.x = rotation;
        gafuCube.mesh.rotation.y = rotation;

        if (time > lastState.time + 3000) {
          lastState.direction.x *= -1;
          lastState.direction.y *= -1;
          lastState.direction.z *= -1;
          lastState.time = time;
        }
        gafuCube.mesh.position.x += moveSpeed * lastState.direction.x;
        gafuCube.mesh.position.y += moveSpeed * lastState.direction.y;
        gafuCube.mesh.position.z += moveSpeed * lastState.direction.z;
      });
    });
    this.gafu.play();
  },
  beforeDestroy() {
    this.gafu.stop();
    this.gafuCubes.forEach(gafuCube => {
      gafuCube.mesh.geometry.dispose();
      gafuCube.mesh = null;
    });
    this.gafu.disposeScene();
  }
};
</script>
<style lang="scss">
.gafu {
  &__wrapper {
    height: 100%;
  }
  &__container {
    height: 100%;
    margin: auto;
    canvas {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}
</style>
