import * as THREE from "three";
import { Cube } from "@/modules/gafu/services/meshes/Cube";
import { PositionHelpers } from "@/modules/gafu/services/util/PositionHelpers";

type GafuCameraType = "perspective" | "";

interface GafuCameraOption {
  type: GafuCameraType;
  fov: number;
  aspect: number;
  near: number;
  far: number;
}
export interface GafuOptions {
  camera: GafuCameraOption;
  light: LightOptions;
}

export interface CameraPositionOptions {
  position?: {
    x?: number;
    y?: number;
    z?: number;
  };
  rotation?: {
    x?: number;
    y?: number;
    z?: number;
  };
}

export interface LightOptions {
  color: number;
  intensity: number;
  type: string;
  position: {
    x: number;
    y: number;
    z: number;
  };
}

export class GafuService {
  renderer: THREE.WebGLRenderer;
  camera: THREE.PerspectiveCamera;
  scene: THREE.Scene;
  light: THREE.DirectionalLight;

  defaultOptions: GafuOptions = {
    camera: {
      type: "perspective",
      fov: 120,
      aspect: 2,
      near: 0.1,
      far: 50
    },
    light: {
      type: "directional",
      color: 0xffffff,
      intensity: 1,
      position: {
        x: -1,
        y: 2,
        z: 4
      }
    }
  };

  isPlaying = false;
  nextFrameUpdaterFunctions: Function[] = [];
  objects: Record<string, any> = {};
  objectIds: string[] = [];

  constructor(private canvasElement: HTMLCanvasElement, options?: GafuOptions) {
    this.renderer = new THREE.WebGLRenderer({
      canvas: this.canvasElement
    });

    if (!options) {
      options = this.defaultOptions;
    }

    this.camera = this.createCamera(options.camera);
    this.light = this.createLight(options.light);
    this.scene = new THREE.Scene();
  }

  addObject(position: any, color: number) {
    const cube = new Cube(color);
    PositionHelpers.setPosition(cube.mesh.position, position);
    this.scene.add(cube.mesh);
    this.objectIds.push(cube.id);
    this.objects[cube.id] = cube.mesh;
    return cube;
  }

  addLight() {
    this.scene.add(this.light);
  }

  render(time: number) {
    if (this.resizeRendererToDisplaySize()) {
      const canvas = this.renderer.domElement;
      this.camera.aspect = canvas.clientWidth / canvas.clientHeight;
      this.camera.updateProjectionMatrix();
    }

    this.nextFrameUpdater(time);

    this.renderer.render(this.scene, this.camera);
    if (this.isPlaying) {
      requestAnimationFrame(this.render.bind(this));
    }
  }

  resizeRendererToDisplaySize() {
    const canvas = this.renderer.domElement;
    const pixelRatio = window.devicePixelRatio;
    const width = (canvas.clientWidth * pixelRatio) | 0;
    const height = (canvas.clientHeight * pixelRatio) | 0;
    const needResize = canvas.width !== width || canvas.height !== height;
    if (needResize) {
      this.renderer.setSize(width, height, false);
    }
    return needResize;
  }

  setNextFrameUpdater(fn: Function | Function[]) {
    this.nextFrameUpdaterFunctions = typeof fn === "function" ? [fn] : fn;
  }

  nextFrameUpdater(time: number) {
    this.nextFrameUpdaterFunctions.forEach(fn => {
      fn(time);
    });
  }

  play() {
    this.isPlaying = true;
    requestAnimationFrame(this.render.bind(this));
  }

  stop() {
    this.isPlaying = false;
  }

  createCamera(cameraOption: GafuCameraOption) {
    switch (cameraOption.type) {
      case "perspective":
      default:
        return new THREE.PerspectiveCamera(
          cameraOption.fov,
          cameraOption.aspect,
          cameraOption.near,
          cameraOption.far
        );
    }
  }

  setCamera(options: CameraPositionOptions) {
    if (options.position) {
      const newCameraPosition = {
        x:
          options.position.x !== undefined
            ? options.position.x
            : this.camera.position.x,
        y:
          options.position.y !== undefined
            ? options.position.y
            : this.camera.position.y,
        z:
          options.position.z !== undefined
            ? options.position.z
            : this.camera.position.z
      };
      this.camera.position.set(
        newCameraPosition.x,
        newCameraPosition.y,
        newCameraPosition.z
      );
    }
  }

  createLight(options: LightOptions) {
    const { color, intensity, type, position } = options;
    let light;
    switch (type) {
      case "directional":
      default:
        light = new THREE.DirectionalLight(color, intensity);
        break;
    }
    light.position.set(position.x, position.y, position.z);
    return light;
  }

  disposeScene() {
    this.scene.dispose();
  }
}
